<template lang="pug">
  div#service
    div.container
      h1.service__title {{ $t('generic.title') }} 

      div.service__card
        div.service__card--each(
          v-for="(value, key, index) in serviceList"
          :key="key"
        )
          h3.service__card--title {{ value.title }}
          p.service__card--text {{ value.desc }}
</template>

<script>
export default {
  name: 'customer',

  data () {
    return {
      serviceList: {
        upload: {
          title: ' AS  ',
          desc: "' '       .     AS  ."
        },
        costs: {
          title: ',   ',
          desc: ' ,       .        .'
        },
        corporate: {
          title: '   ',
          desc: "',  ? ?'  ,    .      .  ."
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.service__card {
  grid-gap: $grid12x !important;

  .service__card--each {
    text-align: center;
  }
}
</style>



